.NavBarLink {
    float: left;
}

.ActiveNavBarLink {
    float: left;
}

.NavBarLink a {
    display: block;
    color: #F8F7FF;
    text-align: center;
    padding: 15px 30px 10px 30px;
    text-decoration: none;
    font-size: x-large;
    font-variant: small-caps;
    transition: padding 0.2s
}

.ActiveNavBarLink a {
    display: block;
    color: #1C1018;
    background-color: #F8F7FF;
    text-align: center;
    padding: 15px 30px 10px 30px;
    text-decoration: none;
    font-size: x-large;
    font-variant: small-caps;
    font-style: italic;
    transition: padding 0.2s;
}

.NavBarLink a:hover {
    background-color: #57C6EA;
    padding: 15px 40px 10px 40px;
}